.header {
  margin: 0;
}

* {
  box-sizing: border-box;
}

body {
  background: linear-gradient(#8a2387, #e94057, #f27121);
  background-repeat: no-repeat;
  min-height: 112vh;
}

.banner {
  position: relative;
  height: 300px;
  margin: 160px auto;
  color: #fff;
}

ul {
  padding-bottom: 20px;
}

.banner img {
  display: block;
  width: 200px;
  height: 200px;
  margin: 50px auto;
  border-radius: 50%;
  -webkit-box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.32);
  -moz-box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.32);
  box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.32);
  animation: logo 2s infinite;
}

.banner h2 {
  position: absolute;
  left: 42%;
  font-size: 2em;
  color: #fff;
}

.banner h2 span {
  color: #0f0;
  display: none;
}

.banner h2 span:nth-child(1) {
  display: initial;
}

.cv {
  position: absolute;
  width: 150px;
  height: 55px;
  top: 350px;
  left: 46%;
  text-align: center;
  line-height: 55px;
  text-transform: uppercase;
  color: #fff;
  background: #be6493;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  letter-spacing: 2px;
  overflow: hidden;
  -webkit-box-shadow: 0px 0px 23px -5px rgba(0, 0, 0, 1);
  -moz-box-shadow: 0px 0px 23px -5px rgba(0, 0, 0, 1);
  box-shadow: 0px 0px 23px -5px rgba(0, 0, 0, 1);
}

.cv .cv-btn {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  z-index: 100000;
  transition: 0s ease-in;
}

.cv:hover .cv-btn {
  transition: 0.4s ease-in;
  transform: translateY(-100%);
}

.cv:before {
  content: "";
  position: absolute;
  bottom: -250px;
  left: 50%;
  transform: translateX(-50%);
  width: 250px;
  height: 250px;
  background: #2299ff;
  border-radius: 50%;
  transition: 0.4s ease-in;
}

.cv:hover::before {
  bottom: -150px;
}

nav {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  text-align: right;
  overflow: hidden;
  background: rgb(255, 255, 255, 0);
  color: #fff;
  z-index: 1000;
  transition: 0.6s ease-in-out;
}

nav.sticky {
  background: rgba(34, 153, 255, 0.2);
}

nav.sticky .navbar li.navbar-link {
  padding: 0;
}

nav.sticky .logo.mLogo {
  padding: 0;
}

.logo {
  float: left;
  list-style: none;
}

.logo.mLogo {
  text-decoration: none;
  color: #fff;
  font-size: 20px;
  padding: 20px;
  cursor: pointer;
  transition: 0.6s;
}

.logo.mLogo:hover {
  color: #2299ff;
}

.navbar li.navbar-link {
  color: #fff;
  text-decoration: none;
  font-size: 20px;
  padding: 10px;
  transition: 0.3s;
  display: inline-block;
  overflow: hidden;
  list-style: none;
  margin-right: 50px;
  color: #fff;
  cursor: pointer;
}

.navbar li.navbar-link:hover {
  color: #2299ff;
}

.container {
  display: block;
  width: 50%;
  margin: 120px auto;
  padding: 20px;
  -webkit-box-shadow: -3px -3px 2px rgba(255, 255, 255, 0.3),
    5px 5px 5px rgba(0, 0, 0, 0.2), 15px 15px 15px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: -3px -3px 2px rgba(255, 255, 255, 0.3),
    5px 5px 5px rgba(0, 0, 0, 0.2), 15px 15px 15px rgba(0, 0, 0, 0.2);
  box-shadow: -3px -3px 2px rgba(255, 255, 255, 0.3),
    5px 5px 5px rgba(0, 0, 0, 0.2), 15px 15px 15px rgba(0, 0, 0, 0.2);
}

.container h2,
.container p {
  text-align: center;
  color: #fff;
}

.container p {
  font-size: 20px;
}

.link_github {
  text-decoration: none;
}

.link_github p {
  font-size: 20px;
}

.action {
  position: fixed;
  bottom: 50px;
  left: 50px;
  width: 50px;
  height: 50px;
  background: #fff;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
}

.action .tombol {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #7d6cfc;
  font-size: 2em;
  transition: 0.3s ease-in-out;
}

.action.active .tombol {
  transform: rotate(90deg);
}

.action ul {
  position: absolute;
  bottom: 55px;
  background: #fff;
  min-width: 250px;
  padding: 20px;
  border-radius: 20px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  list-style: none;
}

.action.active ul {
  bottom: 65px;
  opacity: 1;
  visibility: visible;
  transition: 0.3s;
}

.action.active ul li {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px 0;
  transition: 0.3s;
}

.action ul li a {
  text-decoration: none;
}

.action ul li:not(:last-child) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.action ul li img {
  width: 30px;
  margin-right: 10px;
  opacity: 0.2;
  vertical-align: middle;
}

.action ul li:hover img {
  opacity: 0.8;
}

/* The actual timeline (the vertical ruler) */
.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

/* The actual timeline (the vertical ruler) */
.timeline::after {
  content: "";
  position: absolute;
  width: 6px;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
}

/* Container around content */
.timeline-container {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%;
}

/* The circles on the timeline */
.timeline-container::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  right: -17px;
  background-color: white;
  border: 4px solid #ff9f55;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}

/* Place the container to the left */
.left {
  left: 0;
}

/* Place the container to the right */
.right {
  left: 50%;
}

/* Add arrows to the left container (pointing right) */
.left::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid white;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent white;
}

/* Add arrows to the right container (pointing left) */
.right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid white;
  border-width: 10px 10px 10px 0;
  border-color: transparent white transparent transparent;
}

/* Fix the circle for containers on the right side */
.right::after {
  left: -16px;
}

/* The actual content */
.content {
  padding: 20px 30px;
  background: #be6493;
  position: relative;
  border-radius: 6px;
}

.content p,
.content h2 {
  color: #fff;
}

.skills {
  position: relative;
  display: flex;
  margin: 20px 0;
  padding: 24px 10px 18px;
  background: #be6493;
  border-radius: 8px;
  overflow: hidden;
  transition: 0.5s;
}

.skills::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background: rgba(255, 255, 255, 0.1);
  z-index: 10;
}

.skill-name {
  position: relative;
  width: 110px;
  text-align: right;
  color: #fff;
  margin-top: -2px;
  text-transform: uppercase;
}

.value {
  position: relative;
  width: 40px;
  text-align: left;
  color: #fff;
  margin-top: -2px;
  text-transform: uppercase;
}

.persen {
  position: relative;
  width: calc(100% - 150px);
  height: 20px;
  margin: 0 10px;
  border-radius: 10px;
  background: #fff;
  box-shadow: inset 0 0 10px #000;
  overflow: hidden;
}

.progress {
  position: absolute;
  top: 0;
  left: 0;
  width: 70%;
  height: 100%;
  border-radius: 10px;
  background: #2299ff;
  box-shadow: inset 0 0 2px #000;
  animation: skill 4s ease-in-out forwards;
}

.progress.html:nth-child(1) {
  width: 95%;
}

.progress.css:nth-child(1) {
  width: 85%;
}

.progress.js:nth-child(1) {
  width: 80%;
}

.progress.php:nth-child(1) {
  width: 85%;
}

.progress.mysql:nth-child(1) {
  width: 90%;
}

.progress.laravel:nth-child(1) {
  width: 85%;
}

.project {
  position: relative;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.project {
  position: relative;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.project .card {
  position: relative;
  width: 320px;
  height: 450px;
  border-radius: 20px;
  overflow: hidden;
  margin: 100px 20px;
  -webkit-box-shadow: 15px 15px 15px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 15px 15px 15px rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.project .card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #be6493;
  clip-path: circle(150px at 80% 20%);
  transition: 0.5s ease-in-out;
}

.project .card:hover:before {
  clip-path: circle(300px at 80% -20%);
}

.project .card::after {
  content: attr(data-after-content);
  position: absolute;
  top: 30%;
  left: -20%;
  font-size: 12em;
  font-weight: 800;
  font-style: italic;
  color: rgba(255, 255, 255, 0.04);
}

.project .card .imgBx {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10000;
  width: 100%;
  height: 220px;
  transition: 0.85s;
}

.project .card:hover .imgBx {
  top: 0%;
  transform: translateY(0);
}

.project .card .imgBx img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50px);
  width: 170px;
  height: 150px;
}

.project .card .contentBx {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  text-align: center;
  transition: 1s;
  z-index: 10;
}

.project .card:hover .contentBx {
  height: 210px;
  transform: translateY(-20px);
}

.project .card .contentBx a span:nth-child(1) {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: #d9a7c7; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #fffcdc,
    #d9a7c7
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #fffcdc,
    #d9a7c7
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  animation: projectbtn1 2s linear infinite;
}

@keyframes projectbtn1 {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.project .card .contentBx a span:nth-child(2) {
  position: absolute;
  top: 0;
  right: 0;
  width: 2px;
  height: 100%;
  background: #d9a7c7; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to bottom,
    #fffcdc,
    #d9a7c7
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom #fffcdc,
    #d9a7c7
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  animation: projectbtn2 2s linear infinite;
}

@keyframes projectbtn2 {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(100%);
  }
}

.project .card .contentBx a span:nth-child(3) {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: #d9a7c7; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #fffcdc,
    #d9a7c7
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left #fffcdc,
    #d9a7c7
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  animation: projectbtn3 2s linear infinite;
}

@keyframes projectbtn3 {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

.project .card .contentBx a span:nth-child(4) {
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 100%;
  background: #d9a7c7; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right #fffcdc,
    #d9a7c7
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #fffcdc,
    #d9a7c7
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  animation: projectbtn4 2s linear infinite;
}

@keyframes projectbtn4 {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(-100%);
  }
}

.project .card .contentBx h2 {
  position: relative;
  font-weight: 600;
  letter-spacing: 1px;
  color: #fff;
}

.project .card .contentBx a {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 4px;
  margin-top: 10px;
  background: #be6493;
  text-decoration: none;
  font-weight: 600;
  color: #fff;
  opacity: 0;
  transform: translateY(50px);
  transition: 0.5s;
  overflow: hidden;
}

.project .card:hover .contentBx a {
  opacity: 1;
  transform: translateY(0px);
  transition-delay: 0.5s;
}

@keyframes skill {
  from {
    width: 0;
  }
}

.contact {
  display: flex;
  justify-content: center;
  align-items: center;
}

.email,
.phone,
.github {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 50px;
}

.email img,
.phone img,
.github img {
  width: 50px;
  height: 50px;
}

nav ul .bars {
  transition: 0.6s ease-in-out;
  cursor: pointer;
}

nav ul .bars {
  position: absolute;
  display: block;
  right: 50px;
  top: 35px;
}

nav.sticky ul .bars {
  position: absolute;
  display: block;
  right: 50px;
  top: 15px;
}

nav ul .bars * {
  display: block;
  width: 30px;
  height: 2px;
  background: greenyellow;
  margin: 6px;
  opacity: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}

nav.active ul .bars {
  transform: translateX(-200px);
}

nav ul .bars:hover span:nth-child(1) {
  width: 25px;
  -webkit-transform: rotate(145deg);
  -moz-transform: rotate(145deg);
  -o-transform: rotate(145deg);
  transform: rotate(145deg);
}

nav ul .bars:hover span:nth-child(2) {
  opacity: 1;
  width: 100%;
  transform: translateX(2px);
}

nav ul .bars:hover span:nth-child(3) {
  width: 25px;
  -webkit-transform: rotate(-145deg);
  -moz-transform: rotate(-145deg);
  -o-transform: rotate(-145deg);
  transform: rotate(-145deg);
}

nav.active ul .bars:hover span:nth-child(1) {
  transform-origin: center;
  width: 25px;
  -webkit-transform: rotate(35deg);
  -moz-transform: rotate(35deg);
  -o-transform: rotate(35deg);
  transform: rotate(35deg);
}

nav.active ul .bars:hover span:nth-child(2) {
  opacity: 1;
  width: 90%;
  transform: translateX(-10px);
}

nav.active ul .bars:hover span:nth-child(3) {
  transform-origin: center;
  width: 25px;
  -webkit-transform: rotate(-35deg);
  -moz-transform: rotate(-35deg);
  -o-transform: rotate(-35deg);
  transform: rotate(-35deg);
}

nav.active ul .bars span:nth-child(1) {
  transform-origin: left;
  -webkit-transform: rotate(34deg);
  -moz-transform: rotate(34deg);
  -o-transform: rotate(34deg);
  transform: rotate(34deg);
}

nav.active ul .bars span:nth-child(2) {
  opacity: 0;
  transform: translateX(-60px);
}

nav.active ul .bars span:nth-child(3) {
  transform-origin: left;
  -webkit-transform: rotate(-34deg);
  -moz-transform: rotate(-34deg);
  -o-transform: rotate(-34deg);
  transform: rotate(-34deg);
}

nav.active ul .navbar {
  width: 200px;
}

/* Media queries - Responsive timeline on screens less than 600px wide */
@media screen and (max-width: 1200px) {
  .banner h2 {
    left: 35%;
  }
  .cv {
    left: 42%;
  }
  nav ul .bars * {
    opacity: 1;
  }
  nav ul .navbar {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    line-height: 2;
    overflow-x: hidden;
    transition: 0.6s ease-in-out;
    background: #654ea3; /* fallback for old browsers */
    background: -webkit-linear-gradient(
      to bottom,
      #eaafc8,
      #654ea3
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
      to bottom,
      #eaafc8,
      #654ea3
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    padding-top: 50px;
  }

  nav ul .navbar .navbar-link {
    display: block;
  }
  .contact {
    flex-direction: column;
  }
  /* Place the timelime to the left */
  .timeline::after {
    left: 31px;
  }

  /* Full-width containers */
  .timeline-container {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }

  /* Make sure that all arrows are pointing leftwards */
  .timeline-container::before {
    left: 60px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }

  /* Make sure all circles are at the same spot */
  .left::after,
  .right::after {
    left: 15px;
  }

  /* Make all right containers behave like the left ones */
  .right {
    left: 0%;
  }
}

@keyframes logo {
  0% {
    -webkit-box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.32);
    -moz-box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.32);
    box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.32);
  }
  50% {
    -webkit-box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.82);
    -moz-box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.82);
    box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.82);
  }
  100% {
    -webkit-box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.32);
    -moz-box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.32);
    box-shadow: 0px 0px 29px 13px rgba(0, 0, 0, 0.32);
  }
}
